.library{
    position: fixed;
    top: 0%;
    left: 0%;
    height: 100%;
    width: 20rem;
    background-color: white;
    box-shadow: 2px 2px 50px rgb(204, 204, 204);
    overflow: scroll;
    //Slide out Library
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
}
.library-song{
    display: flex;
    align-items: center;
    padding: 1rem 2rem;
    cursor: pointer;
    transition: background-color 0.5s ease;
    img{
        width: 30%;
    }
    &:hover{
        background-color: rgb(222, 222, 222);
    }
}
.song-description{
    padding-left: 1rem;
    h3{
        font-size: 1rem;
    }
    h4{
        font-size: 0.7rem;
    }
}
*{
    scrollbar-width: thin;
    scrollbar-color: rgba (155, 155, 155, 0.5) transparent;
}
*::-webkit-scrollbar{
    width: 5px;
}
*::-webkit-scrollbar-track{
    background: transparent;
}
*::-webkit-scrollbar-thumb{
    background-color: rgba($color: rgb(155, 155, 155), $alpha: 0.5);
    border-radius: 20px;
    border: transparent;
}
.selected{
    background-color: rgb(101, 219, 248);
}
//Slide back LIBRARY
.active-library{
    transform: translateX(0%);
    opacity: 1;
}

@media screen and (max-width: 768px){
    .library{
        width: 100%;
    }
}